import "./App.css";
import logo from "./Arabic.png";
import { useEffect, useState } from "react";
import axios from "axios";

function App() {
  var [registered, setRegistered] = useState(false);

  var [name, setName] = useState("");
  var [course, setCourse] = useState("");
  var [college, setCollege] = useState("");
  var [email, setEmail] = useState("");
  var [phone, setPhone] = useState("");
  var [whatsapp, setWhatsApp] = useState("");
  var [districtID, setDistrictID] = useState(-1);
  var [zoneID, setZoneID] = useState(-1);

  var [otherCourse, setOtherCourse] = useState(false);
  var [errorMsg, setErrorMsg] = useState("");

  var [districts, setDistrict] = useState([]);
  var [zones, setZone] = useState([]);

  useEffect(() => {
    fetchDistricts();
  }, []);

  async function fetchDistricts() {
    var formData = new FormData();
    formData.append(
      "userData",
      '[{"table":"districts","type":"org","lastModTime":"","limit":"","app_version":"1.3.9","id":"","levelId":"13","list_required":""}]'
    );
    var response = await axios.post(
      "http://apis.guide.wisdomislam.org/common_data/getDataList.php",
      formData
    );
    setDistrict(response.data["districts"]);
  }

  async function fetchZones(districtID) {
    setZoneID(-1);
    districtID++;
    var formData = new FormData();
    formData.append(
      "userData",
      `[{"table":"zones","type":"org","lastModTime":"","limit":"","app_version":"1.3.9","id":"","levelId":"${districtID}","list_required":""}]`
    );
    var response = await axios.post(
      "http://apis.guide.wisdomislam.org/common_data/getDataList.php",
      formData
    );
    setZone(response.data["zones"]);
  }

  function renderDistricts() {
    return districts.map(function (name, index) {
      return <option value={index}>{name["district"]}</option>;
    });
  }

  function renderZones() {
    return zones.map(function (name, index) {
      return <option value={index}>{name["zone"]}</option>;
    });
  }

  function validateData() {
    if (name === "") {
      setErrorMsg("Name is required");
      return false;
    } else if (course === "") {
      setErrorMsg(otherCourse ? "Specify Course" : "Select Course");
      return false;
    } else if (college === "") {
      setErrorMsg("College is required");
      return false;
    } else if (email === "") {
      setErrorMsg("Email is required");
      return false;
    } else if (!email.includes("@") || !email.includes(".")) {
      setErrorMsg("Enter a valid email");
      return false;
    } else if (phone === "") {
      setErrorMsg("Phone is required");
      return false;
    } else if (phone.length < 10) {
      setErrorMsg("Enter a valid phone number");
      return false;
    } else if (districtID === -1) {
      setErrorMsg("Select District & Area");
      return false;
    } else if (zoneID === -1) {
      setErrorMsg("Select Area");
      return false;
    }

    setErrorMsg("");
    return true;
  }

  async function register() {
    var valid = validateData();
    if (valid === false) {
      return;
    }

    var response = await axios.post("http://localhost:7000/register", {
      name: name,
      course: course,
      college: college,
      districtID: districtID,
      zoneID: zoneID,
      email: email,
      phone: phone,
      whatsapp: whatsapp,
    });

    if (response.data["success"] === false) {
      setErrorMsg(response.data["msg"]);
      setRegistered(false);
      return;
    }

    setRegistered(true);
  }

  return (
    <div className="App">
      <div
        className="registered"
        style={{ display: registered ? "flex" : "none" }}
      >
        <img
          src={logo}
          style={{ width: "50%" }}
          className="s"
          alt="Arabic Students Meet Logo"
        />
        <h1 style={{ marginTop: "1rem" }}>Registered Successfully!</h1>
        <input
          style={{ width: "50%", marginTop: "3rem" }}
          onClick={() => {
            window.location.reload();
          }}
          type="submit"
          value="Register again"
        ></input>
      </div>
      <div className="main" style={{ display: registered ? "none" : "block" }}>
        <div className="column">
          <div className="row">
            <div className="left">
              <div className="title">
                <img
                  src={logo}
                  className="logo"
                  alt="Arabic Students Meet Logo"
                />
                <h1 style={{ display: "flex", alignItems: "center" }}>
                  Registration
                </h1>
              </div>
              <div className="field">
                <label>
                  Name
                  <small className="star">*</small>
                </label>
                <input
                  onChange={(x) => {
                    setName(x.target.value);
                  }}
                  type="text"
                  required
                ></input>
              </div>
              <div className="field">
                <label>
                  Course
                  <small className="star">*</small>
                </label>
                <select
                  onChange={(x) => {
                    if (x.target.value === "other") {
                      setOtherCourse(true);
                      setCourse("");
                      return;
                    }
                    setOtherCourse(false);
                    setCourse(x.target.value);
                  }}
                  required
                >
                  <option value="x" defaultValue>
                    Select Course
                  </option>
                  <option value="Afzal Ul Ulama Preliminary">
                    Afzal Ul Ulama Preliminary
                  </option>
                  <option value="BA Afzal Ul Ulama">BA Afzal Ul Ulama</option>
                  <option value="BA Arabic">BA Arabic</option>
                  <option value="BA Functional Arabic">
                    BA Functional Arabic
                  </option>
                  <option value="MA Arabic">MA Arabic</option>
                  <option value="MA Post Afzal Ul Ulama">
                    MA Post Afzal Ul Ulama
                  </option>
                  <option value="MPhil/PhD in Arabic">
                    MPhil/PhD in Arabic
                  </option>
                  <option value="other">Diploma, specify</option>
                  <option value="other">
                    Other Arabic Language Courses, specify
                  </option>
                </select>
              </div>
              {otherCourse ? (
                <div className="field">
                  <label>
                    Specify Course
                    <small className="star">*</small>
                  </label>
                  <input
                    onChange={(x) => {
                      setCourse(x.target.value);
                    }}
                    type="text"
                    required
                  ></input>
                </div>
              ) : (
                ""
              )}
              <div className="field">
                <label>
                  College
                  <small className="star">*</small>
                </label>
                <input
                  onChange={(x) => {
                    setCollege(x.target.value);
                  }}
                  type="text"
                  required
                ></input>
              </div>
              <div className="field">
                <label>
                  District
                  <small className="star">*</small>
                </label>
                <select
                  onChange={(x) => {
                    fetchZones(x.target.value);
                  }}
                  required
                >
                  <option
                    value="x"
                    defaultValue
                    disabled={districts.length !== 0}
                  >
                    Select District
                  </option>
                  {renderDistricts()}
                </select>
              </div>
              <div className="field">
                <label>
                  Area
                  <small className="star">*</small>
                </label>
                <select
                  onChange={(x) => {
                    setZoneID(zones[x.target.value]["id"]);
                    setDistrictID(zones[x.target.value]["district_id"]);
                  }}
                  required
                >
                  <option value="0" defaultValue disabled={zones.length !== 0}>
                    Select Area
                  </option>
                  {renderZones()}
                </select>
              </div>
            </div>
            <div className="right">
              <h1 className="hide">.</h1>
              <div className="field">
                <label>
                  Email
                  <small className="star">*</small>
                </label>
                <input
                  onChange={(x) => {
                    setEmail(x.target.value);
                  }}
                  type="text"
                  required
                ></input>
              </div>
              <div className="field">
                <label>
                  Phone
                  <small className="star">*</small>
                </label>
                <input
                  type="tel"
                  onChange={(x) => {
                    setPhone(x.target.value);
                  }}
                ></input>
              </div>
              <div className="field">
                <label>WhatsApp</label>
                <input
                  type="tel"
                  onChange={(x) => {
                    setWhatsApp(x.target.value);
                  }}
                ></input>
              </div>
              <div className="field">
                <p className="hide">.</p>
                <p
                  style={{
                    color: "red",
                    display: errorMsg === "" ? "none" : "block",
                  }}
                >
                  {errorMsg}
                </p>
                <input
                  onClick={() => register()}
                  type="submit"
                  value="REGISTER"
                ></input>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
